body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: center;

  padding-bottom: 1rem;
  background-color: #4F0AE8;
  text-align: center;

  height: 100vh;
  width: 100%;
}

.full-name {
  font-size: 5rem;
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  color: #ffffff;
}

.occupation {
  color: #c4bcbc;
  font-size: 3.5rem;
}

.btn-rounded-white {
  display: inline-block;
  color: #fff;
  font-size: 1.5rem;
  padding: 0.1rem 1rem;
  border: 3px solid #fff;
  border-radius: 1.5rem;
  -webkit-transition: .5s ease all;
  transition: .5s ease all;
}

.btn-rounded-white:hover {
  text-decoration: none;
  background-color: #fff;
  -webkit-transition: .5s ease all;
  transition: .5s ease all;
}

@media(max-width: 768px){
  .full-name {
    font-size: 3rem;
  }
  .occupation {
    text-align: center;
    font-size: 1.5rem;
  }
  .btn-rounded-white {
    border: 2px solid #fff;
  }
}
.side-drawer {
  height: 100vh;
  background-color: #ffffff;
  box-shadow: 1px 0px 7px rgb(0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 80%;
  max-width: 400px;
  z-index: 200;

  -webkit-transform: translateX(-102%);

          transform: translateX(-102%); /* if is 100% the border shadow will appear. */
  -webkit-transition: -webkit-transform 0.3s ease-out;
  transition: -webkit-transform 0.3s ease-out;
  transition: transform 0.3s ease-out;
  transition: transform 0.3s ease-out, -webkit-transform 0.3s ease-out;
}

.side-drawer.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
}

.side-drawer ul {
  height: 100%;
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.side-drawer li {
  margin: 0.5rem 0;
  width: 70%;
  border-bottom-color: #4F0AE8;
  border-bottom-width: 0.5px;
  border-bottom-style: solid;
}

.side-drawer a {
  color: #4F0AE8;
  text-decoration: none;
  font-size: 1.5rem;
}

.side-drawer a:hover,
.side-drawer a:active{
  color: #97baf1;
  text-decoration: none;
}

@media (min-width: 769px){
 .side-drawer{
   display: none;
 }
}
.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  padding: 1rem;
  padding-left: 5rem;
  padding-right: 5rem;
  background-color: #fff;
}


.footer-left, .footer-right-icon{
  font-size: 1.5rem;
  text-decoration: none;
  color: #606060;
}

@media(max-width: 768px){
  .footer-container {
    justify-content: center;
  }
  .footer-left, .footer-right-icon{
    width: 100%;
  }

}
.toggle-button {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 1.5rem;
  width: 1.5rem;
  background-color: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  box-sizing: border-box;
}

.toggle-button:focus {
  outline: none;
}

.toggle-button-line {
  width: 1.5rem;
  height: 1.5px;
  background-color: white;
}
.menu {
  display: flex;
  position: fixed;
  justify-content: center;
  top: 0;
  
  padding: 0 1rem;
  height: 10%;
  width: 100%;
  
  z-index: 1;
}

.menu-navigation{
  display: flex;
  justify-content: center;
  align-items: center;
  
  padding: 1rem; 
  height: 100%;
  width: auto;
  
  border-bottom-left-radius: 0.3rem;
  border-bottom-right-radius: 0.3rem;
  background-color: #113c83;
}

.menu-navigation-items ul{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  
  font-size: 1.2rem;
}

.menu-navigation-items li{
  padding: 0 0.5rem;
}

.menu-navigation-items a{
  color: #ffffff;
  text-decoration: none;
}

.menu-navigation-items a:active,
.menu-navigation-items a:hover{
  color: rgb(172, 159, 214);
}

@media(max-width: 768px) {
  .menu-navigation-items {
    display: none;
  }
  .menu {
    top:2%;
    justify-content: flex-end;
  }
  .menu-navigation{
    border-radius: 0.3rem;
    height: 2.5rem;
    width: 3rem;
  }
}

@media(min-width: 769px){
  .menu-toogle-button {
    display: none;
  }
  
  .menu-logo {
    margin-left: 0; 
  }
}
.about-container {
  display: flex;
  flex-direction: column;
  
  padding-bottom: 1rem;
  background-color: #fff;
}

.about-container-title{
  font-size: 3rem;
  text-align: center;
}

.about-container-title::first-letter {
  font-size: 4.5rem;
  font-style: italic;
  font-weight: normal;
}

.about-container-text {
  display: flex;
  justify-content: center;
}

.about-container-text p {
  width: 60%;
  font-size: 1.2rem;
  text-align: justify;
  text-indent: 1rem;
}

@media(max-width: 768px) {
  .about-container-text p {
    width: 90%;
    text-indent: 0;
    text-align: left;
  }
}
.education-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #cee1ff;
  padding-bottom: 1rem;
}

.education-title{
  font-size: 3rem;
}

.education-title::first-letter {
  font-size: 4.5rem;
  font-style: italic;
}

.education-card {
  width: 60%;
  
  padding: 2rem;
  margin-bottom: 1rem;

  background-color: #fff;
  border-radius: 0.1rem;
  border-width: 1px;
  box-shadow: 0px 0px 1px 1px #d1d1d1;

  font-size: 1.2rem;
}

.education-card-institution {
  /* font-size: 1.2rem; */
  font-weight: bold;
}

.education-card-end-date {
  /* font-size: 1.2rem;  */
  color: #7e8a80;
  float: right;
}

.education-card-course {
  /* font-size: 1.2rem; */
}

@media(max-width:768px){
  .education-card {
    width: 90%;
  }
  .education-card-end-date{
    width: 100%;
  }
}
.experience-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #F5F7FF;
  padding-bottom: 1rem;
}

.experience-title {
  font-size: 3rem;
}

.experience-title::first-letter {
  font-size: 4.5rem;
  font-style: italic;
}

.experience-card {
  width: 60%;
  
  padding: 2rem;
  padding-bottom: 0.5rem;
  margin-bottom: 1rem;

  background-color: #fff;
  
  line-height: 1rem;
  border-radius: 0.2rem;
  border-width: 1px;
  box-shadow: 0px 0px 2px 1px #d1d1d1;

  font-size: 1.2;
}

.experience-card-title {
  font-weight: bold;
  line-height: 2rem;
}

.experience-card-title span{
  font-weight: normal;
  color: #808080;
  float: right;
}

.experience-card-occupation {
  color: grey;
}

.experience-card-text {
  line-height: 2rem;
}

@media (max-width: 768px){
  .experience-card {
    width: 90%;
  }

  .experience-card-occupation {
    line-height: 1.5rem;
  }

  .experience-card-title span {
    width: 100%;
    margin-bottom: 0.5rem;
  }
}
.skills-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #cee1ff;
  padding-bottom: 1rem;
}

.skills-title {
  font-size: 3rem;
  text-align: center;
}

.skills-title::first-letter {
  font-size: 4.5rem;
  font-style: italic;
}

.skills-card {
  width: 60%;
  margin: 1rem;
  margin-top: 0;
  padding-top: 1rem;
  text-align: center;
}

.skill-item {
  padding: 0.5rem;
  margin: 0.2rem;
  border-radius: 0.1rem;
  font-size: 1.2rem;
  background-color: #C7BBE8;
}

@media(max-width: 768px){
  .skills-card{
    width: 90%;
  }
}
.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #4F0AE8;
  padding-bottom: 1rem;
}

.contact-title {
  font-size: 3rem;
  color: #ffffff;
  text-align: center;
}

.contact-title::first-letter {
  font-size: 4.5rem;
  font-style: italic;
}

.contact-form {
  display: flex;
  flex-direction: column;
  width: 40%;
}

.contact-form input{
  font-size: 1.2em;
  height: 10vh;
  
  color: #FFFFFF;
  background-color: #113c83;
  
  padding: 0.5rem;
  margin-bottom: 0.5rem;
  
  border-width: 0;
  border-radius: 0.3rem;
}

.contact-form input::-webkit-input-placeholder{
  color: #FFFFFF;
}

.contact-form input::-ms-input-placeholder{
  color: #FFFFFF;
}

.contact-form input::placeholder{
  color: #FFFFFF;
}

.contact-form textarea{
  font-size: 1.2rem;
  height: 10rem;
  
  color: #ffffff;
  background-color: #113c83;

  padding: 0.5rem;
  margin-bottom: 0.5rem;
  
  border-width: 0;
  border-radius: 0.3rem;
}

.contact-form textarea::-webkit-input-placeholder{
  color: #FFFFFF;
}

.contact-form textarea::-ms-input-placeholder{
  color: #FFFFFF;
}

.contact-form textarea::placeholder{
  color: #FFFFFF;
}

.contact-form button{
  width: 100%;
  height: 3rem;
  align-items: center;

  color: #4F0AE8;
  font-size: 1.2rem;
  font-weight: bold;

  border-width: 0;
  border-radius: 0.3rem;
  background-color: #ffffff;
}

@media(max-width: 768px){
  .contact-form{
    width: 90%;
  }
}
.backdrop {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgb(0,0,0,0.3);

  top: 0;
  left: 0;
  z-index: 100;
}
body, html {
  font-family: 'Roboto Slab', serif;
  scroll-behavior: smooth;
}
